.home {
	background-color: #fff;
	position: relative;
	margin: 20px auto;
	width: calc(100% - 4rem);
	max-width: 72rem;
	z-index: 2;
	padding: 30px;
	min-height: 100vh;
	box-sizing: border-box;
}

@media screen and (max-width: 600px) {
	.home {
		margin: 0;
		width: 100%;
	}
}
@primary-color: red;@highlight-color: red;